import { useEffect, useState } from 'react'
import Axios from "axios";
export default function UseThoiLuongDanhGia(maPhim) {
  const [data, setData] = useState({ thoiLuong: '120', danhGia: '10' })
  const url = `https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinLichChieuPhim?MaPhim=${maPhim}`
  useEffect(() => {
    let getInfoFlimCancel = Axios.CancelToken.source(); // Axios cung cp,  cancel gi api khi component b hy(bm chuyn cm rp khc)
    const loadData = async () => {
      try { // bt li khi get API, nu c li th vo catch
        const response = await Axios.get(url, {
          cancelToken: getInfoFlimCancel.token
        });
        setData({
          thoiLuong: response.data?.heThongRapChieu?.[0]?.cumRapChieu?.[0]?.lichChieuPhim?.[0]?.thoiLuong, // tch ra thi lng phim
          danhGia: response.data.danhGia
        });
      } catch (error) { // vo y khi c li get api hoc khi cancel thnh cng
        if (Axios.isCancel(error)) { // cancel request thnh cng
          // console.log("AxiosCancel: caught cancel");
        } else {
          throw error; // bo li get api
        }
      }
    };
    loadData();
    return () => {
      getInfoFlimCancel.cancel(); // unmounting th cancel request axios
    };
  }, [])
  return { thoiLuong: data.thoiLuong, danhGia: data.danhGia }
}
