import { useEffect, useState } from 'react'
import Axios from "axios";
export default function UseThoiLuongDanhGia(maLichChieu) {
  const [data, setData] = useState({ diaChi: 'loading...' })
  const url = `https://movie0706.cybersoft.edu.vn/api/QuanLyDatVe/LayDanhSachPhongVe?MaLichChieu=${maLichChieu}`
  useEffect(() => {
    if (!maLichChieu) {
      return
    }
    let getInfoFlimCancel = Axios.CancelToken.source(); // Axios cung cp,  cancel gi api khi component b hy(bm chuyn cm rp khc)
    const loadData = async () => {
      try { // bt li khi get API, nu c li th vo catch
        const response = await Axios.get(url, {
          cancelToken: getInfoFlimCancel.token
        });
        setData({
          diaChi: response.data?.thongTinPhim?.diaChi, // tch ra a ch
        });
      } catch (error) { // vo y khi c li get api hoc khi cancel thnh cng
        if (Axios.isCancel(error)) { // cancel request thnh cng
          // console.log("AxiosCancel: caught cancel");
        } else {
          throw error; // bo li get api
        }
      }
    };
    loadData();
    return () => {
      getInfoFlimCancel.cancel(); // unmounting th cancel request axios
    };
  }, [])
  return { diaChi: data.diaChi }
}
